"use client"
export default function ProductCard({ product }) {
    const maxStars = 5;
    return (
        <div className="p-4 bg-gray-500">
            <div className="w-[295px] h-[298px] rounded-3xl overflow-hidden">
                <img src={product.pictures[0]} alt={product.name} className="w-full " />
            </div>
            <div className="mt-2 ml-2">
                <h2 className="text-lg font-semibold">{product.name}</h2>
                {[...Array(maxStars)].map((_, i) => (
                    <FaStar
                        key={i}
                        className={i < product.rating ? "text-yellow-400" : "text-gray-300"}
                    />
                ))}
                <p className="mt-1">{product.rating}/5</p>
                <p className="mt-1 font-bold text-lg">${product.price}</p>
            </div>
        </div>
    )
}